@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  transition: all 0.3s ease;
}

body {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1a1a1a;
  background-image: radial-gradient(circle, #3b0b0b, #1a1a1a);
}

::selection {
  color: #f2f2f2;
  background: #f86d8d;
}

.tools-container {
  width: 80%;
  max-width: 1000px;
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.7);
  border-top: 1px solid rgba(255, 0, 0, 0.5);
  border-left: 1px solid rgba(255, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  border-radius: 25px;
  padding: 30px;
  overflow: hidden;
  position: relative;
}

.tools-container h2 {
  font-size: 36px;
  font-weight: 600;
  color: #ff4d4d;
  text-align: center;
  margin-bottom: 20px;
}

.tools-list {
  list-style: none;
  padding: 0;
  margin-top: 20px;
}

.tools-list li {
  margin: 20px 0;
}

.tools-list h3 {
  color: #ff4d4d; /* Light color for better contrast */
  font-size: 24px;
  margin-bottom: 10px;
}

.tools-list a {
  color: #f2f2f2; /* Light text color for better contrast */
  text-decoration: none;
  font-size: 18px;
}

.tools-list a:hover {
  color: #ff4d4d;
  text-decoration: underline;
}

@media (max-width: 850px) {
  .tools-container {
    padding: 20px;
  }

  h2 {
    font-size: 28px;
  }

  .tools-list h3 {
    font-size: 20px;
  }

  .tools-list a {
    font-size: 16px;
  }
}

@media (max-width: 410px) {
  .tools-container {
    width: 100%;
    border-radius: 0px;
  }

  h2 {
    font-size: 24px;
  }

  .tools-list h3 {
    font-size: 18px;
  }

  .tools-list a {
    font-size: 14px;
  }
}
