@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  transition: all 0.3s ease;
}

body {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1a1a1a;
  background-image: radial-gradient(circle, #3b0b0b, #1a1a1a);
}

.forgot-password {
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.7);
  border-top: 1px solid rgba(255, 0, 0, 0.5);
  border-left: 1px solid rgba(255, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  padding: 30px;
  border-radius: 15px;
  width: 100%;
  max-width: 400px;
  text-align: center;
}

.forgot-password h1 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #ff0000;
}

.forgot-password input {
  width: calc(100% - 20px);
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 8px;
  border: 1px solid rgba(255, 0, 0, 0.5);
  outline: none;
  background-color: #2a2a2a;
  color: #fff;
}

.forgot-password input::placeholder {
  color: #bbb;
}

.forgot-password button {
  background: linear-gradient(135deg, #ff4d4d, #b30000);
  border: none;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  border-radius: 8px;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px; /* Ensuring space below the button */
}

.forgot-password button:hover {
  background: linear-gradient(135deg, #b30000, #660000);
  transform: scale(0.98);
}

.forgot-password .message {
  margin-top: 20px;
  font-size: 16px;
}

.forgot-password .message.success {
  color: #28a745;
}

.forgot-password .message.error {
  color: #dc3545;
}

.forgot-password a {
  display: inline-block;
  margin-top: 20px;
  text-decoration: none;
  color: #ff0000;
  font-size: 14px;
}

.forgot-password a:hover {
  text-decoration: underline;
}

@media (max-width: 850px) {
  .forgot-password {
    padding: 20px;
    max-width: 90%;
  }

  .forgot-password h1 {
    font-size: 20px;
  }

  .forgot-password input {
    width: 100%;
  }

  .forgot-password button {
    font-size: 16px;
    padding: 8px 15px;
  }

  .forgot-password .message {
    font-size: 14px;
  }

  .forgot-password a {
    font-size: 14px;
  }
}

@media (max-width: 410px) {
  .forgot-password {
    padding: 15px;
    max-width: 90%;
    border-radius: 15px;
  }

  .forgot-password h1 {
    font-size: 18px;
  }

  .forgot-password input {
    padding: 8px;
  }

  .forgot-password button {
    font-size: 14px;
    padding: 8px 12px;
  }

  .forgot-password .message {
    font-size: 12px;
  }

  .forgot-password a {
    font-size: 12px;
  }
}
