@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  transition: all 0.3s ease;
}

body {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1a1a1a;
  background-image: radial-gradient(circle, #3b0b0b, #1a1a1a);
}

.tutorials-container {
  width: 90%;
  max-width: 800px;
  margin: auto;
  background-image: radial-gradient(circle, #3b0b0b, #1a1a1a);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.7);
  border-top: 1px solid rgba(255, 0, 0, 0.5);
  border-left: 1px solid rgba(255, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  border-radius: 25px;
  padding: 30px;
  color: #f2f2f2;
}

.tutorials-container h2 {
  text-align: center;
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
}

.tutorials-list {
  list-style: none;
  padding: 0;
}

.tutorials-list li {
  margin-bottom: 20px;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  transition: box-shadow 0.3s ease;
}

.tutorials-list li:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.tutorials-list h3 {
  margin: 0 0 10px 0;
  color: #ff4d4d;
  font-size: 20px;
}

.tutorials-list a {
  color: #f2f2f2;
  text-decoration: none;
  font-size: 16px;
}

.tutorials-list a:hover {
  text-decoration: underline;
}

@media (max-width: 850px) {
  .tutorials-container {
    padding: 20px;
  }

  .tutorials-container h2 {
    font-size: 28px;
  }
}

@media (max-width: 410px) {
  .tutorials-container {
    padding: 15px;
  }

  .tutorials-container h2 {
    font-size: 24px;
  }

  .tutorials-list h3 {
    font-size: 18px;
  }

  .tutorials-list a {
    font-size: 14px;
  }
}
