@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  transition: all 0.3s ease;
}

body {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(135deg, #1a1a1a 10%, #3b0b0b 100%);
}

::selection {
  color: #f2f2f2;
  background: #ff4d4d;
}

.add-money-container {
  width: 100%;
  max-width: 500px;
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.7);
  border-top: 1px solid rgba(255, 0, 0, 0.5);
  border-left: 1px solid rgba(255, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  z-index: 12;
  border-radius: 25px;
  padding: 30px;
  overflow: hidden;
  position: relative;
}

.add-money-text {
  font-size: 36px;
  font-weight: 500;
  color: #db0303;
  text-align: center;
  margin-bottom: 0px;
}

p {
  font-size: 16px;
  text-align: center;
  margin-top: 10px;
}

strong {
  font-weight: 600;
}

.form-group {
  margin-top: 20px;
}

.form-group label {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.form-group select, .form-group input {
  width: 80%;
  max-width: 400px;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid rgba(255, 0, 0, 0.5);
  border-radius: 10px;
  background-color: #2a2a2a;
  color: #fff;
  font-size: 16px;
}

.btn-submit {
  width: 80%;
  max-width: 200px;
  padding: 10px;
  margin: 20px auto;
  border: none;
  border-radius: 10px;
  background: linear-gradient(135deg, #ff4d4d, #b30000);
  color: #f2f2f2;
  font-size: 18px;
  font-weight: 400;
  cursor: pointer;
  display: block;
  text-align: center;
  transition: transform 0.3s ease;
}

.btn-submit:hover {
  background: linear-gradient(135deg, #b30000, #660000);
  transform: scale(0.97);
}

.message {
  font-size: 16px;
  text-align: center;
  margin-top: 10px;
}

.message.error {
  color: #dc3545;
}

.message.success {
  color: #28a745;
}

.number {
  font-size: 22px;
  font-weight: 300;
  color: #9be9ad;
  text-align: center;
  transition: transform 0.3s ease;
  cursor: pointer;
}


@media (max-width: 850px) {
  .add-money-container {
    padding: 20px;
  }

  h1 {
    font-size: 28px;
  }

  .form-group select, .form-group input, .btn-submit {
    width: 100%;
  }
}

@media (max-width: 410px) {
  .add-money-container {
    width: 100%;
    border-radius: 0;
  }

  h1 {
    font-size: 24px;
  }

  .form-group select, .form-group input, .btn-submit {
    font-size: 14px;
    padding: 8px;
  }
}

.text1 {
    font: 1em sans-serif;
    color: #ff0000;
}