@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  transition: all 0.3s ease;
}

body {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1a1a1a;
  background-image: radial-gradient(circle, #3b0b0b, #1a1a1a);
}

::selection {
  color: #f2f2f2;
  background: #f86d8d;
}

header {
  height: 85vh;
  width: 90%;
  background-image: radial-gradient(circle, #3b0b0b, #1a1a1a);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.7);
  border-top: 1px solid rgba(255, 0, 0, 0.5);
  border-left: 1px solid rgba(255, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  z-index: 12;
  border-radius: 25px;
  margin: auto;
  position: relative;
  display: flex;
  flex-direction: column;
}

.navbar {
  width: 100%;
  padding: 20px 50px;
  border-radius: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #3b0b0b, #1a1a1a;
}

.navbar .logo a {
  text-decoration: none;
  font-size: 24px;
  color: #db2f2f;
  border-radius: 10px;
  border-color: #f2f2f2;
  font-weight: 600;
}

.navbar .menu {
  display: flex;
  flex-wrap: wrap;
  padding-left: 50px;
}

.navbar .menu li {
  list-style: none;
  padding-left: 10px;
  margin: 0 10px;
}

.navbar .menu a {
  color: #f2f2f2;
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  transition: all 0.3s ease;
}

.navbar .menu a:hover {
  color: #ff4d4d;
}

.navbar .buttons button {
  outline: none;
  color: #f2f2f2;
  font-size: 16px;
  font-weight: 400;
  border-radius: 12px;
  padding: 5px 15px;
  border: none;
  margin: 0 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: linear-gradient(135deg, #ad2020, #b30000);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.navbar .buttons button:hover {
  background: linear-gradient(135deg, #b30000, #660000);
  transform: scale(0.97);
}

.text-content {
  width: 50%;
  margin: auto;
  color: #f2f2f2;
  text-align: center;
}

.text-content h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
}

.text-content p {
  font-size: 18px;
  margin-top: 20px;
  line-height: 1.5;
}

@media (max-width: 850px) {
  .navbar {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 15px 5px;
  }

  .navbar .menu {
    margin: 10px 0 20px 0;
  }

  .text-content {
    width: 70%;
  }

  .text-content h2 {
    font-size: 28px;
  }
}

@media (max-width: 410px) {
  header {
    height: 100vh;
    width: 100%;
    border-radius: 0px;
  }

  .navbar {
    padding: 15px 10px;
  }

  .text-content {
    width: 90%;
  }

  .text-content h2 {
    font-size: 24px;
  }

  .text-content p {
    font-size: 16px;
  }
}
