@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  transition: all 0.3s ease;
}

body {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1a1a1a;
  background-image: radial-gradient(circle, #3b0b0b, #1a1a1a);
}

.container {
  background: rgba(0, 0, 0, 0.8);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.7);
  border-top: 1px solid rgba(255, 0, 0, 0.5);
  border-left: 1px solid rgba(255, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  padding: 40px;
  border-radius: 15px;
  width: 100%;
  max-width: 400px;
  text-align: center;
  margin: 20px;
}

h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #ff0000;
}

.input-container {
  margin-bottom: 20px;
}

.input-container input {
  width: calc(100% - 20px);
  padding: 10px;
  border-radius: 8px;
  border: 1px solid rgba(255, 0, 0, 0.5);
  outline: none;
  background-color: #2a2a2a;
  color: #fff;
}

.input-container input::placeholder {
  color: #bbb;
}

.button {
  background: linear-gradient(135deg, #ad2020, #b30000);
  border: none;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  border-radius: 8px;
  padding: 10px 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 15px;
  width: 65%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.button:hover {
  background: linear-gradient(135deg, #b30000, #660000);
  transform: scale(0.98);
}

.button-google-button {
  background: linear-gradient(135deg, #ad2020, #b30000);
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  border-radius: 8px;
  padding: 8px 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 15px;
  width: 65%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  border: none;
}

.button-google-button:hover {
  background: linear-gradient(135deg, #b30000, #660000);
  transform: scale(0.98);
}

.message {
  margin-top: 20px;
  font-size: 16px;
  color: #dc3545;
}

.message.success {
  color: #28a745;
}

.message.error {
  color: #dc3545;
}

a {
  display: inline-block;
  margin-top: 20px;
  text-decoration: none;
  color: #ff0000;
  font-size: 14px;
}

a:hover {
  text-decoration: underline;
}

@media (max-width: 600px) {
  .container {
    padding: 20px;
  }

  h2 {
    font-size: 20px;
  }

  .input-container input {
    width: calc(100% - 10px);
  }

  .message {
    font-size: 14px;
  }

  a {
    font-size: 12px;
  }
}
